import React from "react";
import PropTypes from "prop-types";
import "../components/Button.css";

export const Button = ({ type, label, style, onClick, disable, ...props }) => {
  return (
    <button
      type="button"
      className={disable ? `button--${type}--disable` : `button--${type}`}
      style={style}
      onClick={onClick}
      disabled={disable}
      {...props}
    >
      {label}
    </button>
  );
};

Button.propTypes = {
  type: PropTypes.string,
  label: PropTypes.string,
  style: PropTypes.object,
  onClick: PropTypes.func,
  disable: PropTypes.bool,
};

Button.defaultProps = {
  type: "primary",
  label: "Press Me",
  style: { margin: "0px" },
  onClick: undefined,
};

export default Button;
